import React, { Component } from 'react';
import { Row, Form, Input, Col } from 'antd';
import { BtnSearch } from 'components/common/button';

@Form.create({ name: 'formTableQualifiedDoThesis' })
class TableQualifiedDoThesis extends Component {
  constructor(props) {
    super(props);
    this.refTable = React.createRef();
    this.state = {};
  }

  handleFilterByStudentCode = () => {
    const { handleFilterByStudentCode } = this.props;
    const { getFieldValue } = this.props.form;
    const studentCode = getFieldValue('studentCode');
    handleFilterByStudentCode(studentCode);
  }

  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <div>
        <Form className="form-inline-label" layout="vertical">
          <Row gutter={24}>
            <Col span={5}>
              <Form.Item label="Mã sinh viên">
                {getFieldDecorator('studentCode')(
                  <Input />
                )}
              </Form.Item>
            </Col>
            <Col span={5}>
              <div className="btn-group">
                <BtnSearch onClick={this.handleFilterByStudentCode} />
              </div>
            </Col>
          </Row>
        </Form>
      </div>
    );
  }
}

export default TableQualifiedDoThesis;



