import { request, requestDownload } from 'utils';

const prefix = '/api/education';

export const getClassCreditEnrollment = ({
  cCSId,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
} = {}) =>
  request({
    prefix,
    url: '/ClassCreditEnrollment',
    method: 'GET',
    params: {
      cCSId,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
    data: {},
  });

export const getCollectTypes = ({ page, pageSize = 10, orderBy, isPagingEnabled = false } = {}) =>
  request({
    prefix,
    url: '/ClassCreditEnrollment/CollectTypes',
    method: 'GET',
    params: {
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
    data: {},
  });

