export const createClassCreditEnrollment = value =>
  request({
    prefix,
    url: '/ClassCreditEnrollment',
    data: value,
    method: 'POST',
  });

export const updateClassCreditEnrollment = ({ ccsId, isLocked }) => {
  return request({
    prefix,
    url: '/ClassCreditEnrollment',
    data: {
      ccsId,
      isLocked,
    },
    method: 'PUT',
  });
};

export const changeApprovedByCCEId = ({ cceId, isApproved }) => {
  return request({
    prefix,
    url: `/ClassCreditEnrollment/ChangeApprovedByCCEId`,
    data: {
      cceId,
      isApproved,
    },
    method: 'PUT',
  });
};

export const changeApprovedByClassId = ({ ccsId, classId, isApproved }) => {
  return request({
    prefix,
    url: `/ClassCreditEnrollment/ChangeApprovedByClassId`,
    data: {
      ccsId,
      classId,
      isApproved,
    },
    method: 'PUT',
  });
};

export const printListStudent = cCSId =>
  requestDownload({
    prefix,
    defaultFileName: 'Danh sach Phong thi Lop tin chi.pdf',
    url: `/ClassCreditEnrollment/ExportCCEForTesting`,
    params: {
      cCSId,
    },
    method: 'POST',
  });

export const createEnrollCCEs = data =>
  request({
    prefix,
    url: '/ClassCreditEnrollment/EnrollCCEs',
    data,
    method: 'POST',
  });

export const exportExcelCCEByCCSId = data =>
  requestDownload({
    prefix,
    url: `/ClassCreditEnrollment/ExportExcelCCEByCCSId?ccsId=${data.cCSId}`,
    data,
    method: 'POST',
  });

